

<template>
  <a-layout  style="min-width: 1200px">
    <a-layout-content style="padding: 0 50px">
<!--      fan'h返回-->
      <!-- <a-row type="flex" justify="start">
          <a-col :span="1" offset="1">
            <router-link to="/listpage">
            <a-button type="link"><a-icon type="left" />返回</a-button>
            </router-link>
          </a-col>
        </a-row> -->
<!--      面包屑-->
      <a-row>
        <a-col :span="4" offset="1">
          <a-breadcrumb style="margin-top:20px">
        <a-breadcrumb-item><a @click="routerTo(true,'/listpage')">需求管理</a></a-breadcrumb-item>
        <a-breadcrumb-item><a @click="routerTo(false,false)">查看需求</a></a-breadcrumb-item>
        <a-breadcrumb-item v-if="showBread">订单详情</a-breadcrumb-item>
      </a-breadcrumb>
        </a-col>
      </a-row>
      

      <div class="mlist-control ant-list-bordered">
        <div >
          <h2>{{needList.needsFarmerType}}   {{needList.needsDes}}</h2>
          <h4>需求发布时间: {{needList.needsTime}}&emsp;|&emsp;工程开工时间: {{needList.needsBeginTime}}&emsp;|&emsp;工程停工时间: {{needList.needsEndTime}}</h4>
        </div>
        <div style="margin-left: auto;margin-right: 40px">
          <h2 :style="machineStatusStyle(needList.needsType)">{{needList.needsType}}</h2>
        </div>

      </div>

      <a-tabs
        defaultActiveKey="1"
        @change="changeBread"

        style="margin-top:16px;background-color:white;width: 90%;min-width: 1200px;margin-left: auto;margin-right: auto"
      >
        <a-tab-pane tab="需求内容" :key="nowKey" @click="changeBread">
          <a-card style="margin-top: 24px" :bordered="false" title="基本需求"
                  :style="{backgroundImage: 'url(' + img + ')',
                  backgroundRepeat:no-repeat,center,top,
                  backgroundSize:'100% 100%'
                  }">
            <a-collapse>
              <a-collapse-panel
                v-for="items in matchOrderDes"
                :key="items"
                v-bind:header="items.group_leader"
              >
                <a-table
                  :columns="columns"
                  :dataSource="items.members"
                  :pagination="{ pageSize: 3 }"
                ></a-table>
              </a-collapse-panel>
            </a-collapse>
            <br>
            <a-form id="components-form-demo-validate-other" :form="form">
              <a-form-item label="工作地点" :labelCol="{ span: 2 }" :wrapperCol="{ span: 14,offset:1}">
                <span class="ant-form-text">{{needList.needsLocation}}</span>
              </a-form-item>
              <a-form-item label="开工时间" :labelCol="{ span: 2 }" :wrapperCol="{ span: 14,offset:1 }">
                <span class="ant-form-text">{{needList.needsBeginTime}}</span>
              </a-form-item>
              <a-form-item label="交工时间" :labelCol="{ span: 2 }" :wrapperCol="{ span: 14,offset:1 }">
                <span class="ant-form-text">{{needList.needsEndTime}}</span>
              </a-form-item>
              <a-form-item label="总酬劳" :labelCol="{ span: 2 }" :wrapperCol="{ span: 14,offset:1 }">
                <span class="ant-form-text">{{needList.price}}</span>
              </a-form-item>
              <a-form-item label="备注" :labelCol="{ span: 2 }" :wrapperCol="{ span: 14,offset:1 }">
                <p class="ant-form-text">{{needList.remarks}}</p>
              </a-form-item>
            </a-form>
          </a-card>
        </a-tab-pane>

        <a-tab-pane tab="订单详情" key="2" @click="changeBread">
          <a-card
            v-for="(items,index) in matchOrderDes"
            :key="items.id"
            style="margin-top: 24px;margin: 20px"
            :bordered="true"
            v-bind:title="'订单'+(index+1)"
            :style="{backgroundImage: 'url(' + img + ')',
                  backgroundRepeat:no-repeat,center,top,
                  backgroundSize:'100% 100%'
                  }"
          >
            <p
              style="fontSize: 14px;color: rgba(0, 0, 0, 0.85); marginBottom: 16px;fontWeight: 500"
            >{{items.group_leader}} 工头</p>
            <a-table :columns="columns" :dataSource="items.members" :pagination="{ pageSize: 6 }"></a-table>
            <p slot="extra">订单编号：{{items.id}}&emsp; |&emsp;交易时间：{{items.lastModified}}</p>
            <h2 style="color: #901E02;font-weight: bold;text-align:right">订单金额：{{items.money}}</h2>
          </a-card>
            <a-row type="flex" justify="start">
              <a-col span="14" offset="12">
                <a-form  :form="form">
                  <a-form-item label="订单数量" :labelCol="{ span: 4 }" :wrapperCol="{ span: 6,offset:1}">
                    <span class="ant-form-text">{{matchOrderDes.length}}</span>
                  </a-form-item>
                  <a-form-item label="订单总计" :labelCol="{ span: 4 }" :wrapperCol="{ span: 6,offset:1 }">
                    <span class="ant-form-text">{{orderDes.allMoneyToFarmers}}</span>
                  </a-form-item>
                  <a-form-item label="平台服务" :labelCol="{ span: 4 }" :wrapperCol="{ span: 6,offset:1 }">
                    <span class="ant-form-text">{{orderDes.allMoneyToApp}}</span>
                  </a-form-item>
                  <a-form-item label="费用综合" :labelCol="{ span: 4 }" :wrapperCol="{ span: 6,offset:1 }">
                    <p class="ant-form-text" style="color: #901E02;font-weight: bold">{{orderDes.allMoney}}</p>
                  </a-form-item>
                  <a-form-item  >
                    <a-button v-if="needList.needsType === '匹配完成待支付'" type="primary" style="margin-left: 200px" @click="payOrders">支付订单</a-button>
                  </a-form-item>
                </a-form>
              </a-col>
            </a-row>
        </a-tab-pane>
      </a-tabs>
    </a-layout-content>

    <a-layout-footer></a-layout-footer>
  </a-layout>
</template>

<script>
import DetailList from "@/components/DescriptionList";
import { getDemandDetailById, getOrderDetailById, payForOrders } from "../../api/login";

const DetailListItem = DetailList.Item;
const columns = [
  {
    title: "姓名",
    dataIndex: "name",
    key: "name",
    width: "20%"
  },
  {
    title: "身份证号",
    dataIndex: "IDCard",
    key: "IDCard",
    width: "40%"
  },
  {
    title: "联系方式",
    dataIndex: "phoneNumber",
    key: "phoneNumber",
    width: "40%"
  }
];
export default {
  components: {
    DetailList,
    DetailListItem
  },
  data() {
    return {
      showBread: false,
      nowKey: "1",
      columns,
      id: localStorage.getItem("userId"),
      demandId: this.$route.query.id,
      needList: {},
      matchPeopleDes: {},
      matchOrderDes: "",
      orderDes:"",
      img:require('../../assets/dddb-webEnt/outbg.jpg')
    };
  },
  mounted() {
    this.$parent.showMenu=true
    this.getDemandDetail();
    this.getOrderDetail();
  },
  computed: {

    list() {},
    paginationPropsDemand() {
      let pag = {
        pageSize: 3,
        total: this.peopleData.length
      };
      return pag;
    },
    paginationPropsOrder() {
      let pag = {
        pageSize: 6,
        total: this.orderData.length
      };
      return pag;
    },
    peopleData() {
      let list = [...this.matchPeopleDes];
      return list;
    },
    orderData() {
      let list = [...this.matchOrderDes];
      return list;
    }
  },
  methods: {
    routerTo(isPath,linkOrShow){
      if (isPath === true) {
        this.$router.push(linkOrShow)
      }else {
        // this.showBread = linkOrShow
        window.location.reload()
      }
    },
    // 动态样式
    machineStatusStyle(e){
      console.log('值',e)
      if(e == '匹配中'){
        return {
          'color':'#D39846'
        }
      }else if(e == '匹配失败'){
        return {
          'color':'#ABABAA'
        }
      }else if(e == '匹配完成待支付'){
        return {
          'color':'#314659'
        }
      }else if(e == '交易成功'){
        return {
          'color':'#4F5F46'
        }
      };
    },
    changeBread(key) {
      if (key === '1') {
        this.showBread = false;
      }
      else{
        this.showBread = true;
      }
    },
    getDemandDetail() {
      getDemandDetailById(this.demandId)
        .then(response => {
          // console.log(response.data);
          this.needList = response.data.need;
          this.matchPeopleDes = response.data.matchPeople;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    getOrderDetail() {
      getOrderDetailById(this.demandId)
        .then(response => {
          // console.log(response.data);
          this.orderDes = response.data;
          this.matchOrderDes = response.data.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    payOrders(){
      const that = this
      payForOrders({needid:this.demandId})
        .then(response => {
          // console.log(response.data);
          if (response.data.result === 25) {
            alert(response.data.msg)
            that.$router.push('/listpage')
          }
        })
        .catch(err => {
          console.log(err)
        })
    }
  }
};
</script>

<style lang="less" scoped>
  :global(.ant-form label){
    font-weight: bold;
    font-size:18px;
  }
  :global(.ant-form-text){
    font-size:18px;
  }

.text {
  color: rgba(0, 0, 0, 0.45);
}

.heading {
  color: rgba(0, 0, 0, 0.85);
  font-size: 20px;
}

.no-data {
  color: rgba(0, 0, 0, 0.25);
  text-align: center;
  line-height: 64px;
  font-size: 16px;

  i {
    font-size: 24px;
    margin-right: 16px;
    position: relative;
    top: 3px;
  }
}

.mobile {
  .detail-layout {
    margin-left: unset;
  }
  .status-list {
    text-align: left;
  }
}
.list-control {
  background: #fff;
  width: 100%;
  margin: 0 auto;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 16px;
  height: 120px;
  display: flex;
  flex-direction: row;
}
.listpage {
  min-height: 50px;
  background: #fff;
  margin: 0 auto;
  margin-bottom: 20px;
  padding: 16px 10px 0px 10px;
  width: 100%;
}
</style>



